<template>
	<div id="reserve-yuding">
		<public-header title="预定"></public-header>
		<div class="content">
			<div class="yuding-header-wrap">
				<div class="yuding-header">
					<div class="yuding-date" v-for="(dayItem, index) in days" :key='index' :class="{active: index == extra.dayIndex}" @click="onSelectDay(index, dayItem.day)">
						<div>{{dayItem.day_cn}}</div>
						<div class="padding-5-t">{{dayItem.day}} {{dayItem.week_cn}}</div>
					</div>
				</div>
				<div class="yuding-filter van-hairline--top">
					<div class="filter-item" @click="onChangeSearch('tables')">{{extra.SearchCategoryItem}} <i class="icon icon-unfold"></i></div>
					<div class="filter-item" @click="onChangeSearch('times')">{{extra.SearchTime}} <i class="icon icon-unfold"></i></div>
					<div class="filter-item" @click="onChangeSearch('discounts')">全部折扣 <i class="icon icon-unfold"></i></div>
				</div>
			</div>
			<div class="tables-list">
				<div v-for="categoryItem in categorys_new" :key="categoryItem.id">
					<div class="tables-item" v-for="item in reserves_new[categoryItem.id]" :key="item.title">
						<div class="tables-left">
							<div class="tables-info">
								<div class="tables-time">{{item.title}}</div>
								<div class="tables-date">{{days[extra.dayIndex].day_cn}}</div>
								<div class="font-14">{{categoryItem.title}}</div>
							</div>
							<div class="flex">
								<template v-if="tables_info[extra.day]">
									<template v-if="tables_info[extra.day][categoryItem.id]">
										<template v-if="tables_info[extra.day][categoryItem.id][item.time]">
											<div class="tables-bar">
												<div class="bar-inner" :style="{width: (tables_info[extra.day][categoryItem.id][item.time] / item.total_num) * 100 + '%'}"></div>
											</div>
											<template v-if="!item.total_num">
												<div class="tables-text">剩0桌</div>
											</template>
											<template v-else>
												<div class="tables-text">剩{{item.total_num - tables_info[extra.day][categoryItem.id][item.time]}}桌</div>
											</template>
										</template>
										<template v-else>
											<div class="tables-bar">
												<div class="bar-inner" style="width: 0%"></div>
											</div>
											<div class="tables-text">剩{{item.total_num}}桌</div>
										</template>
									</template>
									<template v-else>
										<div class="tables-bar">
											<div class="bar-inner" style="width: 0%"></div>
										</div>
										<div class="tables-text">剩{{item.total_num}}桌</div>
									</template>
								</template>
								<template v-else>
									<div class="tables-bar">
										<div class="bar-inner" style="width: 0%"></div>
									</div>
									<div class="tables-text">剩{{item.total_num}}桌</div>
								</template>
							</div>
						</div>
						<div class="tables-right flex">
							<template v-if="(extra.dayIndex == 0 && item.timeout == 1) || extra.dayIndex > 0">
								<div class="btn-tables" @click="onSelectTime(item.time, categoryItem.id, item.total_num)">预订</div>
							</template>
							<template v-else>
								<div class="btn-tables border-gray" @click="onSelectTime(-1, -1, 0)">预订</div>
							</template>
						</div>
					</div>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<!--筛选弹出层-->
		<van-popup class="popup-search" v-model="extra.showPopupSearch" position="top">
			<div class="search-btn-tab van-hairline--bottom">
				<div class="search-btn-item" :class="{active: extra.searchType == 'tables'}" @click="onChangeSearchType('tables')">全部桌型</div>
				<div class="search-btn-item" :class="{active: extra.searchType == 'times'}" @click="onChangeSearchType('times')">全部时间</div>
				<div class="search-btn-item" :class="{active: extra.searchType == 'discounts'}" @click="onChangeSearchType('discounts')">全部折扣</div>
			</div>
			<template v-if="extra.searchType == 'tables'">
				<div class="search-alltable">
					<div class="new-search-group">
						<div class="new-search-item" @click="onChangeCategory(-1, -1)">
							<div class="info" :class="{active: extra.SearchCategoryId == '-1'}">全部桌型</div>
						</div>
						<div class="new-search-item" v-for="(categoryItem, index) in categorys" :key="index" @click="onChangeCategory(categoryItem.id, index)">
							<div class="info" :class="{active: extra.SearchCategoryId == categoryItem.id}">{{categoryItem.title}}</div>
						</div>
					</div>
				</div>
			</template>
			<template v-else-if="extra.searchType == 'times'">
				<div class="search-alltable">
					<div class="new-search-group">
						<div class="new-search-item" @click="onChangeTimes('全部时间', -1)">
							<div class="info" :class="{active: extra.SearchTime == '全部时间'}">全部时间</div>
						</div>
						<template v-if="extra.SearchCategoryId == '-1'">
							<div class="new-search-item" v-for="(timeItem, index) in reserves[categorys[0].id]" :key="index" @click="onChangeTimes(timeItem.time, index)">
								<div class="info" :class="{active: extra.SearchTime == timeItem.time}">{{timeItem.time}}</div>
							</div>
						</template>
						<template v-else>
							<div class="new-search-item" v-for="(timeItem, index) in reserves[extra.SearchCategoryId]" :key="index" @click="onChangeTimes(timeItem.time, index)">
								<div class="info" :class="{active: extra.SearchTime == timeItem.time}">{{timeItem.time}}</div>
							</div>
						</template>
					</div>
				</div>
			</template>
			<template v-else-if="extra.searchType == 'discounts'">
				<div class="search-alltable">
					<div class="new-search-group">
						<div class="new-search-item">
							<div class="info active">全部折扣</div>
						</div>
					</div>
				</div>
			</template>
		</van-popup>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import PublicHeader from '@/components/header'
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				sid: 0,
				categorys: {},
				categorys_new: {},
				reserves: {},
				reserves_new: {},
				days: [],
				year: '',
				tables_info: {},
				extra: {
					dayIndex: 0,
					day: '',
					time: '',
					cid: '',
					showPopupSearch: false,
					SearchCategoryId: '-1',
					SearchCategoryItem: '全部桌型',
					searchType: '',
					SearchTime: '全部时间'
				},
				showPreLoading: true
			}
		},
		components: {
			PublicHeader
		},
		methods: {
			...mapMutations([
				'setState'
			]),
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'wmall/store/reserve/index',
					data: {
						sid: that.sid
					},
					success: (res) => {
						that.showPreLoading = false;
						that.categorys = res.categorys;
						that.categorys_new = res.categorys;
						that.reserves = res.reserves;
						that.reserves_new = res.reserves;
						that.days = [...res.days];
						that.year = res.year;
						that.extra.day = that.year + '-' + that.days[0]['day'];
						that.tables_info = res.tables_info;
					}
				})
			},
			onSelectDay(index, value) {
				this.extra.dayIndex = index;
				this.extra.day = this.year + '-' + value;
				this.setState({type:'reserveExtra', key:'day', val:this.extra.day});
			},
			onSelectTime(time, cid, total_num) {
				if(time == -1) {
					this.$toast('该时间不能预定点餐');
					return false;
				}
				if(this.tables_info[this.extra.day] && this.tables_info[this.extra.day][cid]) {
					if(total_num <= this.tables_info[this.extra.day][cid][time]) {
						this.$toast('该桌台类型下没有可以预订的桌位');
						return false;
					}
				}
				if(this.extra.dayIndex == 0) {
					this.extra.day = this.year + '-' + this.days[0].day;
					this.setState({type:'reserveExtra', key:'day',val:this.extra.day});
				}
				this.extra.time = time;
				this.extra.cid = cid;
				this.setState({type:'reserveExtra', key:'cid',val:cid});
				this.setState({type:'reserveExtra', key:'time',val:time});
				this.$router.push(this.util.getUrl({path: '/tangshi/pages/reserve/create?sid=' + this.sid}));
			},
			onChangeSearch(type) {
				this.extra.searchType = type;
				this.extra.showPopupSearch = !this.extra.showPopupSearch;
			},
			onChangeSearchType(searchType) {
				if(this.extra.searchType == searchType) {
					return;
				}
				this.extra.searchType = searchType;
			},
			onChangeCategory(id, index) {
				var that = this;
				if(index == '-1') {
					that.reserves_new = {};
					that.reserves_new = that.reserves;
					that.categorys_new = that.categorys;
					that.extra.SearchCategoryItem = '全部桌型';
				} else {
					that.reserves_new = {};
					that.reserves_new[id] = that.reserves[id];
					that.categorys_new = [];
					that.categorys_new.push(that.categorys[index]);
					that.extra.SearchCategoryItem = that.categorys[index].title;
				}
				that.extra.SearchCategoryId = id;
				that.extra.SearchTime = '全部时间';
				this.extra.showPopupSearch = !this.extra.showPopupSearch;
			},
			onChangeTimes(time, index) {
				var that = this;
				if(index == '-1') {
					that.reserves_new = that.reserves;
				} else {
					if(that.extra.SearchCategoryId == '-1') {
						var CategoryId = that.categorys[0].id;
					} else {
						var CategoryId = that.extra.SearchCategoryId;
					}
					var reserve = that.reserves[CategoryId][index];
					that.reserves_new = {};
					that.reserves_new[CategoryId] = [];
					that.reserves_new[CategoryId].push(reserve);
				}
				that.extra.SearchTime = time;
				this.extra.showPopupSearch = !this.extra.showPopupSearch;
			}
		},
		mounted () {
			this.onLoad();
		},
		created() {
			this.query = this.$route.query;
			if(this.query) {
				this.sid = this.query.sid;
			}
		}
	}
</script>

<style>
	#reserve-yuding .yuding-header-wrap{
		position: fixed;
		top: 46px;
		left: 0;
		right: 0;
		z-index: 2;
	}
	#reserve-yuding .yuding-header-wrap .yuding-header{
		width: 100%;
		white-space: nowrap;
		overflow-x: scroll;
		overflow-y: hidden;
		font-size: 0;
		padding: 10px 15px;
		background-color: #fff;
	}
	#reserve-yuding .yuding-header-wrap .yuding-header .yuding-date{
		display: inline-block;
		padding: 5px 20px;
		color: #999;
		font-size: 14px;
		text-align: center;
		margin-right: 10px;
	}
	#reserve-yuding .yuding-header .yuding-date.active{
		border: 1px solid #FFCDB2;
		background-color: #FFF5F0;
		color: #FF5900;
		border-radius: 35px;
	}
	#reserve-yuding .yuding-header .yuding-date:last-child{
		margin-right: 0;
	}
	#reserve-yuding .yuding-filter{
		position: relative;
		padding: 15px;
		font-size: 14px;
		color: #787878;
		background-color: #fff;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
	}
	#reserve-yuding .yuding-filter .filter-item{
		padding: 0 10px;
	}
	#reserve-yuding .tables-list{
		padding: 109px 15px 15px;
	}
	#reserve-yuding .tables-list .tables-item{
		display: flex;
		align-items: center;
		margin-top: 15px;
		padding: 15px 0;
		background-color: #fff;
		box-shadow:2px 2px 5px #e0e0e0;
		border-radius: 5px;
	}
	#reserve-yuding .tables-list .tables-item .tables-left{
		width: 75%;
		padding: 0 15px;
		border-right: 1px dashed #e0e0e0;
	}
	#reserve-yuding .tables-list .tables-item .tables-left .tables-info{
		display: flex;
		align-items: flex-end;
	}
	#reserve-yuding .tables-list .tables-item .tables-left .tables-time{
		font-size: 22px;
		font-weight: bold;
		color: #FF5900;
		padding-right: 10px;
	}
	#reserve-yuding .tables-list .tables-item .tables-left .tables-date{
		font-size: 14px;
		color: #FF5900;
		padding-right: 10px;
	}
	#reserve-yuding .tables-list .tables-item .tables-left .tables-bar{
		width: 65%;
		height: 5px;
		border-radius: 5px;
		background-color: #FFE6D8;
		position: relative;
		margin: 15px 0 5px;
		overflow: hidden;
	}
	#reserve-yuding .tables-list .tables-item .tables-left .tables-bar .bar-inner{
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		bottom: 0;
		border-radius: 20px;
		background: -webkit-linear-gradient(left, #FD6131, #FE0000);
		background: -o-linear-gradient(right, #FD6131, #FE0000);
		background: -moz-linear-gradient(right, #FD6131, #FE0000);
		background: linear-gradient(to right, #FD6131 , #FE0000);
	}
	#reserve-yuding .tables-list .tables-item .tables-left .tables-text{
		font-size: 12px;
		color: #999;
		padding-left: 10px;
		margin-top: 6px;
	}
	#reserve-yuding .tables-list .tables-item .tables-right{
		width: 25%;
	}
	#reserve-yuding .tables-list .tables-item .tables-right .btn-tables{
		background: linear-gradient(to right, #FF9701 , #FF6200);
		color: #fff;
		border-radius: 50px;
		padding: 7px 12px;
		font-size: 12px;
		margin: 0 auto;
	}
	#reserve-yuding .tables-list .tables-item .tables-right .btn-tables.border-gray{
		background: #999;
	}
	#reserve-yuding .popup-search .search-btn-tab{
		padding: 15px 0;
		position: relative;
		background-color: #ffffff;
		display: flex;
		font-size: 14px;
	}
	#reserve-yuding .popup-search{
		margin-left: -1px;
	}
	#reserve-yuding .popup-search .search-btn-tab .search-btn-item{
		position: relative;
		text-align: center;
		color: #333;
		width: 33.33%;
	}
	#reserve-yuding .popup-search .search-btn-tab .search-btn-item.active{
		color: #ff2d4b;
	}
	#reserve-yuding .popup-search .search-alltable{
		width: 100%;
		background-color: #fff;
		padding: 10px 10px 5px 10px;
	}
	#reserve-yuding .popup-search .search-alltable .new-search-group{
		display: flex;
		flex-wrap: wrap;
		position: relative;
		width: 100%;
	}
	#reserve-yuding .popup-search .search-alltable .new-search-group .new-search-item{
		width: 33.33%;
	}
	#reserve-yuding .popup-search .search-alltable .new-search-group .new-search-item .info{
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 14px;
		color: #333;
		padding: 8px 5px;
		background: #fafafa;
		margin: 0 5px 5px;
	}
	#reserve-yuding .popup-search .search-alltable .new-search-group .new-search-item .info.active{
		color: #FF5900;
		background-color: #FFF5F0;
		border-radius: 3px;
	}
</style>


